.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* display and sidebar */

img.delete {
  width: 20px;
  padding-left: 2em;
}

img.edit {
  width: 20px;
  padding-left: 2em;
}

#display { 
  position: absolute;
  top: 60px;
  bottom: 60px;
  height: 82%;
  width: 80%;
  right: 0px;
  overflow: auto;
  }

.form-visible {
  display:none;
}

.button-margin{
  margin-left: 2em;
}
.options-box {
position: absolute;
top: 20px;
background:rgb(71, 27, 27);
height: 100%;
text-align: left;
width: 20%;
z-index: 10;
}

.sidebar-item {
margin-top: 1em;
font-size: 0.7em;
color: yellow;
border: 2px solid blue;
width: 80%;
}

nav {
position: absolute;
top: 0;
width: 100%;
height: 60px;
background-color:rgb(71, 27, 27);
color: yellow;
text-align:center;
}

#footer {
position: fixed;
bottom: 0;
width: 100%;
background-color:rgb(71, 27, 27);
color: yellow;
font-size: 8pt;
letter-spacing: 1px;
padding: 1em;
text-align: center;
text-transform: uppercase;
z-index: 11;
}

.sidebar-item img {
  width: 100%;
  height: 100%;
}

img {
  width: 100%;
}

/* display css */

.listnone {
list-style-type:none;
padding: 0;
}

.listbold {
  font-weight: bold;
}
.spacing {
	padding: 1em 0 1em 0;
}

.dborder {
	border: 1px solid #333;
	border-radius: 10px;
  padding: 1em;
}

.dborder2 {
  border: 1px solid #333;
  border-radius: 10px;
  padding: 0 0 0 1em;

}

.dborder3 {
	border: 1px solid #333;
	border-radius: 10px;
  padding: 0 0 0 1em
}

.boxshw {
box-shadow: 15px 10px hsla(258, 91%, 9%, 0.945);
}

.cardwi {
	margin: auto;
  width: 80%;
  background-color: #70c5ea;
}
