body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #70c5ea; */
  background-image: url("https://3c1703fe8d.site.internapcdn.net/newman/csz/news/800/2016/57e3d4dbd417f.jpg");
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


/* display and sidebar */

img.delete {
  width: 20px;
  padding-left: 2em;
}

img.edit {
  width: 20px;
  padding-left: 2em;
}

#display { 
  position: absolute;
  top: 60px;
  bottom: 60px;
  height: 82%;
  width: 80%;
  right: 0px;
  overflow: auto;
  }

.form-visible {
  display:none;
}

.button-margin{
  margin-left: 2em;
}
.options-box {
position: absolute;
top: 20px;
background:rgb(71, 27, 27);
height: 100%;
text-align: left;
width: 20%;
z-index: 10;
}

.sidebar-item {
margin-top: 1em;
font-size: 0.7em;
color: yellow;
border: 2px solid blue;
width: 80%;
}

nav {
position: absolute;
top: 0;
width: 100%;
height: 60px;
background-color:rgb(71, 27, 27);
color: yellow;
text-align:center;
}

#footer {
position: fixed;
bottom: 0;
width: 100%;
background-color:rgb(71, 27, 27);
color: yellow;
font-size: 8pt;
letter-spacing: 1px;
padding: 1em;
text-align: center;
text-transform: uppercase;
z-index: 11;
}

.sidebar-item img {
  width: 100%;
  height: 100%;
}

img {
  width: 100%;
}

/* display css */

.listnone {
list-style-type:none;
padding: 0;
}

.listbold {
  font-weight: bold;
}
.spacing {
	padding: 1em 0 1em 0;
}

.dborder {
	border: 1px solid #333;
	border-radius: 10px;
  padding: 1em;
}

.dborder2 {
  border: 1px solid #333;
  border-radius: 10px;
  padding: 0 0 0 1em;

}

.dborder3 {
	border: 1px solid #333;
	border-radius: 10px;
  padding: 0 0 0 1em
}

.boxshw {
box-shadow: 15px 10px hsla(258, 91%, 9%, 0.945);
}

.cardwi {
	margin: auto;
  width: 80%;
  background-color: #70c5ea;
}

.content {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (min-width: 800px) {
.options-box {
    display: inline-block; 
    padding: 1em;
    z-index: 10;

}

.sidebar-item {
  padding-left: 1em;
  font-size: 1em;
  }

#display { 
width: 73%;
left: 10%;
}

.sidebar-item img {
  width: 70%;
  height: 70%;
}
img {
  width: 100%;
}
.cardwi {
	margin: auto;
	width: 50%;
	background-color: #70c5ea;
}
}


.form-view > label {
  padding: 0px 0px 10px;
  color:yellow;
  font-weight: bold;
}

input {
  margin: 1em 0 0 1em;
  text-align: left;
}

#add-form {
  position: absolute;
  top: 60px;
  bottom: 0;
  height: 80%;
  width: 80%;
  right: 10%;
  text-align: center;
}

h2 {
  color: white;
}
